@keyframes leftToRight {
    0% {
        opacity: 0;
        transform: translateX(-10rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes RightToLeft {
    0% {
        opacity: 0;
        transform: translateX(10rem); }
    100% {
        opacity: 1;
        transform: translateX(0px); } }

@keyframes BtmToTop {
    0% {
        opacity: 0;
        transform: translateY(5rem); }
    100% {
        opacity: 1;
        transform: translateY(0px); } }
